export function getAllDocumentParents(document, items) {
  var getParent = function getParent(document) {
    return items.find(function (item) {
      return item.uuid === document.parentUuid;
    });
  };

  var currentDocument = document;
  var parents = [];

  while (currentDocument && currentDocument.parentUuid) {
    var parent = getParent(currentDocument);

    if (parent) {
      parents.push(parent);
    }

    currentDocument = parent;
  }

  return parents;
}